import { Badge } from "@/components/ui/badge";
import { Calendar } from "lucide-react";
import { format } from "date-fns";

export function AchievementCard({ achievement, onViewDetails }) {
  return (
    <div className="relative z-10 rounded-lg border bg-white shadow-md hover:shadow-lg transition-shadow overflow-hidden">
      {/* Achievement Image/Banner */}
      <div className="relative h-48 overflow-hidden bg-gradient-to-r from-blue-500 to-purple-500">
        {/* Static image path */}
        <img
          src={achievement.image}
          alt={achievement.name}
          className="object-cover w-full h-full"
        />
      </div>

      {/* Achievement Details */}
      <div className="p-4 space-y-4">
        <div>
          <h3 className="font-semibold text-lg line-clamp-1">{achievement.name}</h3>
          <p className="text-sm text-muted-foreground line-clamp-2 mt-1">
            {achievement.description}
          </p>
        </div>
        <div className="mt-2">
              <div className="flex flex-wrap gap-2">
                {achievement.hashtags?.map((hashtag, index) => (
                  <Badge
                    key={index}
                    variant="secondary"
                    className="bg-black text-white text-xs px-3 py-1 rounded-md"
                  >
                    {hashtag}
                  </Badge>
                ))}
              </div>
            </div>
        <div className="space-y-2">
          {/* Handle invalid date values */}
          {achievement.date && !isNaN(new Date(achievement.date)) ? (
            <div className="flex items-center text-sm text-muted-foreground">
              <Calendar className="h-4 w-4 mr-2" />
              <span>{format(new Date(achievement.date), "MMMM d, yyyy")}</span>
            </div>
          ) : (
            <div className="flex items-center text-sm text-muted-foreground">
              <Calendar className="h-4 w-4 mr-2" />
              <span>Unknown Date</span>
            </div>
          )}
        </div>

        {/* View Details Button */}
        <div className="flex justify-end pt-2">
          <button
            className="px-4 py-2 rounded-md bg-red-500 text-white text-md font-semibold shadow-md hover:bg-red-600 transition-all"
            onClick={onViewDetails}
          >
            View Details
          </button>
        </div>
      </div>
    </div>
  );
}
