"use client";

import { useState, useMemo } from "react";
import { Input } from "@/components/ui/input";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Search } from "lucide-react";
import { AchievementCard } from "@/components/achievements/AchievementCard";
import { AchievementDetailsDialog } from "@/components/achievements/AchievementDetailsDialog";
import { CreateAchievementDialog } from "@/components/achievements/CreateAchievementDialog";

const achievements = [
    {
      id: 1,
      name: "1st prize for sustainable development",
      image: "/bees.jpeg",
      description: "TunApi, focuses on enhancing beekeeping practices and ensuring the health and safety of bees using cutting-edge IoT and AI technologies.",
      date: "2025-07-02",
      members: "Nour Ben Ammar, Malek Gharsallah, Nadia Trablesi, Eya Ben Moulehem, Ismail Bokri, Mehdi Fgaier",
      type: "Projects",
      link: "#",
      user: 'x',
      hashtags: ["#sustainability", "#IoT", "#AI", "#beekeeping"],
    },
    {
      id: 2,
      name: "Baby Name Generator",
      image: "/name.jpeg",
      description: "Created an AI-driven baby name generator tailored for African countries.",
      date: "2025-02-24",
      members: "Malek, Alice",
      type: "Hackathons",
      link: "#",
      user: 'x',
      hashtags: ["#AI", "#babynamegenerator", "#Africa"],
    },
    {
      id: 3,
      name: "23 Weeks",
      image: "/23WeeksHackathon.png",
      description: "Won 2nd place for an AI-based fetal health monitoring system.",
      date: "2025-02-23",
      members: "Malek Gharsallah, Nour Ben Ammar, Eya Ben Moulehem",
      type: "Projects",
      link: "#",
      user: 'x',
      hashtags: ["#AI", "#healthcare", "#fetalmonitoring"],
    },
    {
      id: 4,
      name: "AI for Social Good",
      image: "/AIforGood.jpeg",
      description: "Developed a predictive model to improve maternal healthcare accessibility.",
      date: "2025-02-22",
      members: "John, Sarah",
      type: "Surveys",
      link: "#",
      user: 'x',
      hashtags: ["#AI", "#maternalhealth", "#socialgood"],
    },
    {
      id: 5,
      name: "Survey on AI in Prenatal Care",
      image: "/23WEEKs.jpeg",
      description: "Comprehensive review of AI techniques for fetal ultrasound analysis.",
      date: "2025-02-21",
      members: "Sarah",
      type: "Hackathons",
      link: "#",
      user: 'x',
      hashtags: ["#AI", "#prenatalcare", "#ultrasound"],
    },
    {
      id: 6,
      name: "Deep Learning for Fetal Brain Anomaly Detection",
      image: "/brain.jpeg",
      description: "Published research on using YOLO for detecting fetal brain anomalies.",
      date: "2025-02-20",
      members: "Alice, Sarah",
      type: "publications",
      link: "#",
      user: 'x',
      hashtags: ["#AI", "#deeplearning", "#fetalhealth", "#YOLO"],
    },
  ];

export default function AchievementsPage() {
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedAchievement, setSelectedAchievement] = useState(null);
  const [activeTab, setActiveTab] = useState("all");
  const [showCreateDialog, setShowCreateDialog] = useState(false);

  // Filter achievements based on tab selection and search term
  const filteredAchievements = useMemo(() => {
    return achievements
      .filter((achievement) => {
        if (
          activeTab !== "all" &&
          achievement.type.toLowerCase() !== activeTab.toLowerCase()
        ) {
          return false;
        }
        if (
          searchTerm &&
          !achievement.name.toLowerCase().includes(searchTerm.toLowerCase())
        ) {
          return false;
        }
        return true;
      })
      .sort((a, b) => new Date(b.date) - new Date(a.date)); // Sort by date (newest first)
  }, [searchTerm, activeTab]);

  // Open Achievement Details Dialog
  const handleViewDetails = (achievement) => {
    setSelectedAchievement(achievement);
  };

  // Close Achievement Details Dialog
  const handleCloseDetails = () => {
    setSelectedAchievement(null);
  };

  return (
    <div className="space-y-6 relative">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">
            Academic Contributions
          </h1>
          <p className="text-muted-foreground">
            Explore projects, hackathons, surveys, and publications
          </p>
        </div>
      </div>

      {/* Search Bar */}
      <div className="flex items-center gap-4">
        <div className="relative w-full">
          <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
          <Input
            placeholder="Search achievements..."
            className="w-full pl-10 py-2 text-lg border-2 border-gray-300 focus:border-blue-500"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
        
        {/* Plus Button to Add New Achievement */}
        <button
          className="flex items-center justify-center w-14 h-14 text-3xl bg-red-500 text-white rounded-full hover:bg-red-600 transition-shadow shadow-lg"
          onClick={() => setShowCreateDialog(true)}
        >
          +
        </button>
      </div>

      {/* Tabs & Achievements List */}
      <div className={selectedAchievement ? "opacity-50 pointer-events-none" : ""}>
        <Tabs value={activeTab} onValueChange={setActiveTab} className="space-y-6">
          <TabsList>
            <TabsTrigger value="all">All</TabsTrigger>
            <TabsTrigger value="projects">Projects</TabsTrigger>
            <TabsTrigger value="hackathons">Hackathons</TabsTrigger>
            <TabsTrigger value="surveys">Surveys</TabsTrigger>
            <TabsTrigger value="publications">My Publications</TabsTrigger>
          </TabsList>

          <TabsContent value={activeTab} className="space-y-4">
            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
              {filteredAchievements.map((achievement) => (
                <AchievementCard
                  key={achievement.id}
                  achievement={achievement}
                  onViewDetails={() => handleViewDetails(achievement)}
                />
              ))}
            </div>
          </TabsContent>
        </Tabs>
      </div>

      {/* Achievement Details Dialog */}
      {selectedAchievement && (
        <AchievementDetailsDialog
          achievement={selectedAchievement}
          onClose={handleCloseDetails}
        />
      )}

      {/* Create Achievement Dialog */}
      {showCreateDialog && (
        <CreateAchievementDialog
          open={showCreateDialog}
          onOpenChange={setShowCreateDialog}
        />
      )}
    </div>
  );
}
