import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";

export function AchievementDetailsDialog({ achievement, onClose }) {
  if (!achievement) return null; // Return nothing if no achievement is selected

  return (
    <>
      {/* Faint background overlay */}
      <div className="fixed inset-0 bg-gray-500 bg-opacity-50 z-40"></div>

      {/* Modal - Achievement Details */}
      <div className="fixed inset-0 flex items-center justify-center z-50">
        <div className="bg-white p-6 rounded-lg shadow-lg max-w-lg w-full opacity-100">
          {/* Date */}
          <p className="text-sm text-gray-250">{new Date(achievement.date).toLocaleDateString()}</p>

          {/* Achievement Name */}
          <h2 className="text-2xl font-bold mt-2">{achievement.name}</h2>

          {/* Image */}
          <img
            src={achievement.image}
            alt={achievement.name}
            className="mt-4 w-full h-48 object-cover rounded-md"
          />

          {/* Description */}
          <p className="mt-4 text-sm text-muted-foreground">{achievement.description}</p>

          {/* Members and Hashtags */}
          <div className="mt-4 text-sm text-gray-500">
            <div>
              <h3 className="font-semibold">Members:</h3>
              <p>{achievement.members}</p>
            </div>
            <div className="mt-2">
              <div className="flex flex-wrap gap-2">
                {achievement.hashtags?.map((hashtag, index) => (
                  <Badge
                    key={index}
                    variant="secondary"
                    className="bg-black text-white text-xs px-3 py-1 rounded-md"
                  >
                    {hashtag}
                  </Badge>
                ))}
              </div>
            </div>
          </div>

          <Button onClick={onClose} className="mt-4 w-full">
            Close
          </Button>
        </div>
      </div>
    </>
  );
}
